<template>
    <el-container v-if="type=== 'iFun'">
        <el-aside width="65%">
            <el-input placeholder="中文标题" v-model="form.zhTitle"/>

            <el-input placeholder="英文标题" v-model="form.enTitle"/>

            <el-input
                    type="textarea"
                    autosize
                    placeholder="摘要"
                    v-model="form.abstract">
            </el-input>

            <el-select value=""
                       v-model="form.label"
                       multiple
                       filterable
                       allow-create
                       default-first-option
                       placeholder="请选择标签">
                <el-option
                        v-for="item in labelList"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                </el-option>
            </el-select>

            <el-input placeholder="中文字幕" v-model="form.zhVvt"/>

            <el-input placeholder="英文字幕" v-model="form.enVvt"/>
        </el-aside>
        <el-main>
            <el-select placeholder="分类" v-model="form.class" value="" size="mini">
                <el-option
                        v-for="(index,value) in iFunTypeList"
                        :key="value"
                        :label="index"
                        :value="value">
                </el-option>
            </el-select>


            <el-select placeholder="难度" v-model="form.difficulty" value="" size="mini">
                <el-option label="初级" value="simple"/>
                <el-option label="中级" value="easily"/>
                <el-option label="高级" value="difficulty"/>
            </el-select>
        </el-main>
    </el-container>
</template>

<script>
    export default {
        props: {
            type: String,
            form: {
                type: Object,
                default() {
                    return {}
                }
            },
        },
        data() {
            return {
                labelList: [],
                iFunTypeList: {
                    funny:'搞笑',
                    movie:'电影',
                    entertainment:'娱乐',
                    culture:'文化',
                    travel:'旅行',
                    arts:'艺术',
                    foods:'美食',
                    sports:'运动',
                    technology:'科技',
                }
            }
        },
        mounted() {
        },
    }
</script>

<style scoped>
    .el-main .el-select {
        display: block;
    }

    .el-main {
        padding: 0 20px;
    }

    .el-aside *, .el-main * {
        margin: 15px 0;
    }
</style>