<template>
    <div class="media">
        <div v-if="currentType === 'mp4'" class="media">
            <video controls="" preload="none" poster="" class="media">
                <source :src="fileInfo.fileUrl" type="video/mp4">
            </video>
        </div>

        <div v-if="currentType === 'jpg'|| currentType === 'png'||currentType === 'gif'" class="media">
            <img :src="fileInfo.fileUrl" class="media"/>
        </div>

        <div v-if="currentType === 'mp3'" class="media">
            <audio controls="" preload="none" class="media">
                <source :src='fileInfo.fileUrl'>
            </audio>
        </div>
    </div>
</template>

<script>
    export default {
        props: {
            currentType: String,
            fileInfo: Object,
        }
    }
</script>

<style scoped>
.media{
    width: 100%;
    height: 100%;
}
</style>